.theCard {
    width: 800px;
    box-shadow: 0px 0px 20px 2px lightgrey;
    background-color: transparent;
}

.downbutton {
    margin-top: 22px;
    margin-left: 14px;
    width: 95%;
    align-self: center;
}

.sdrinput {
    margin-bottom: 30px;
}

.inputfields {
    margin: 10px;
}

.circle {
    position: absolute;
    top: -38px;
    left: 50%;
    margin-left: -40px;
    width: 60px !important;
    height: 60px;
}

.steps-content {
    min-height: 200px;
    margin-top: 16px;
    padding-top: 80px;
    text-align: center;
    background-color: #fafafa;
    border: 1px dashed #e9e9e9;
    border-radius: 2px;
}

.steps-action {
    margin-top: 24px;
}

.ant-upload.ant-upload-drag {
    border: none !important;
    margin-bottom: 50px;
}

.captureButton {
    margin-top: 20px;
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #0066b2;
    color: white;
    border-radius: 5px;
    display: inline-block;
}

.deleteButton {
    margin-top: 20px;
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #d77623;
    color: white;
    border-radius: 5px;
    display: inline-block;
}

.imageCanvas {
    margin-top: 20px;
    width: 100%;
    height: 200px;
    /* object-fit: cover; */
    display: flex;
    justify-content: center;
}

.splashScreenlogo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    padding-top: 224px;
}
